.subscriptions {
  position: relative;

  padding: 0 $mobile-indent 68px;

  background-image: url("../img/bg-subs-patterns-mobile@1x.png");
  background-repeat: no-repeat;
  background-position: center 107%;
  background-size: 100%;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/bg-subs-patterns-mobile@2x.png");
    background-size: 307px 274px;
  }

  &::after {
    content: "";
    position: absolute;
    top: 36px;
    left: 0;
    z-index: -1;

    width: 100%;
    height: 100%;

    background-image: url("../img/bg-subs-clouds-mobile@1x.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/bg-subs-clouds-mobile@2x.png");
    }
  }
}

.subscriptions__title {
  margin: 0 0 34px;
}

.subscriptions__form {
  margin-bottom: 30px;
}

.subscriptions__list {
  @include reset-list;
}

.subscriptions__item {



  &:not(:last-child) {
    margin-bottom: 30px;
  }
}

.subscriptions__wrapper {
  padding: 24px 51px 30px;
  text-align: center;

  background-color: $white;
  box-shadow: 0 4px 25px rgba(142, 128, 169, 0.2);
};

.subscriptions__name {
  margin: 0 0 11px;

  font-size: $font-size-l;
  line-height: 125%;
}

.subscriptions__time {
  margin: 0 0 10px;

  color: $second-text;
}

.subscriptions__price {
  display: block;
  margin-bottom: 20px;

  font-weight: bold;
  font-size: $font-size-l + 2px;
  line-height: 20px;
}

.subscriptions__link {
  display: block;
  padding: 10px 23px 9px;

  color: $white;

  background-color: $main-text;
}

@media (min-width: $tablet-width) {
  .subscriptions {
    padding: 0 $tablet-indent - 11px 128px $tablet-indent;

    background-image: none;

    &::after {
      top: -295px;

      height: 1000px;

      background-image: url("../img/bg-subs-clouds-tablet@1x.png");
      background-size: 100%;

      @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
        background-image: url("../img/bg-subs-clouds-tablet@2x.png");
      }
    }
  }

  .subscriptions__title {
    margin-bottom: 21px;
  }

  .subscriptions__form {
    margin-bottom: 41px;
  }

  .subscriptions__list {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }

  .subscriptions__item {
    width: 28.2%;

    &:not(:last-child) {
      margin-bottom: 0;
    }

    &--main {
      width: 35.6%;
    }
  }

  .subscriptions__wrapper {
    padding: 38px 36px 29px;
    width: 100%;
    margin-bottom: 0;

    &--main {
      padding-bottom: 39px;
      width: 100%;

    }
  }
  .subscriptions__name {
    margin-bottom: 7px;

    font-size: $font-size-l + 2px;
  }

  .subscriptions__time {
    margin-bottom: 13px;
  }

  .subscriptions__price {
    margin-bottom: 0;

    font-size: 21px;

    &--main {
      margin-bottom: 19px;
    }
  }

  .subscriptions__link {
    display: none;

    &--main {
      display: block;
    }
  }
}

@media (min-width: $desktop-width) {
  .subscriptions {
    padding: 0 $desktop-indent - 20px 145px $desktop-indent - 21px;

    &::after {
      top: -140px;

      height: 1000px;

      background-image: url("../img/bg-subs-clouds-desktop@1x.png");
      background-size: 100%;

      @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
        background-image: url("../img/bg-subs-clouds-desktop@2x.png");
      }
    }
  }

  .subscriptions__title {
    margin-bottom: 57px;
  }

  .subscriptions__form {
    margin-bottom: 51px;
  }

  .subscriptions__list {
    max-width: 931px;
    margin: 0 auto;

  }

  .subscriptions__item {
    perspective: 1000px;
    transform-style: preserve-3d;
  }

  .subscriptions__wrapper {
    padding: 57px 55px 55px;
    transition: transform 0.2s;
    &--main {
      padding: 57px 54px;
    }
  }
  .subscriptions__name {
    margin-bottom: 18px;
  }

  .subscriptions__time {
    margin-bottom: 25px;

    font-size: $font-size-m;
    line-height: 20px;

    &--main {
      margin-bottom: 28px;
    }
  }

  .subscriptions__price {
    font-size: 30px;

    &--main {
      margin-bottom: 37px;
    }
  }

  .subscriptions__link {
    font-size: $font-size-m;

    transition: background-color 0.3s ease-out;

    &--main {
      padding: 18px 23px 19px;
    }

    &:hover,
    &:focus {
      background-color: $dark-purple;

      animation: pulse-min 0.3s ease-out;
    }

    &:active {
      background-color: $second-text;
    }
  }
}
